import React from 'react';

import image1 from '../../source/wordpresspermisos5.png';
import image2 from '../../source/wordpresspermisos.png';
import image3 from '../../source/wordpresspermisos6.png';
import image4 from '../../source/wordpresspermisos2.png';
import image5 from '../../source/wordpresspermisos3.png';
import image6 from '../../source/wordpresspermisos4.png';


class Wordpress extends React.Component{

  render(){
  
    return (

      <React.Fragment>
        <div className="container col-12">
        <br/><br/>


          <br/>
          <br/><hr></hr><br/>


          <h2  className="bigtitle font">Permisos</h2><br/>
          <br/>

          <ul>
            <br/>
            <center><img src={image1} className="image-permisos-wordpress "  title="imagen permisos wordpress"></img></center>
            <br/><br/>

            <li><p><strong>Usuario: </strong>Lo primero que tenemos que hacer es crear el usuario y contraseña del cliente desde <u className="subrayado">Usuario - crear nuevo</u>. Luego vamos a <u className="subrayado">Editar</u> ese usuario y quitamos la casilla barra de herramientas para impedirle al usuario utilizar Elementor. Es importante definir el <u className="subrayado">perfil</u> en este caso sera "gestor de la tienda"</p></li>

            <br/>
            <center><img src={image2} className="image-permisos-wordpress "  title="imagen permisos wordpress"></img></center>
            <br/><br/>

            <li><p><strong>Plugin: </strong>Mas tarde instalaremos el plugin <u className="subrayado">White Label CMS</u> para darle los permisos.. </p></li>
           
            <br/>
            <center><img src={image3} className="image-permisos-wordpress "  title="imagen permisos wordpress"></img></center>
            <br/><br/>
           
            <li><p><strong>Permisos: </strong>Nos dirigimos al plugin en <u className="subrayado">ajustes - White Label CMS</u> y en menu validamos los permisos que queramos que tenga acceso.</p></li>
            

            <br/>
            <center><img src={image6} className="image-permisos-wordpress "  title="imagen permisos wordpress"></img></center>
            <br/><br/>
           
            <li><p><strong>Permisos: </strong>Nos dirigimos a escritorio y en <u className="subrayado">selecciona los perfiles a los que se ocultaran los paneles del escritorio</u> escribimos el <strong>perfil</strong> del usuario anteriormente creado en este caso "gestor de la tienda".</p></li>
            

            <br/>
            <li><img src={image4} className="image-permisos-wordpress col-12"  title="imagen permisos wordpress"></img></li>
            <br/><br/>
            <br/><br/>
            
            <li><br/><br/><p><strong>Opciones de plantilla: </strong>Entramos dentro del usuario del cliente y vamos entre todas sus opciones desplegando <u className="subrayado">opciones de plantilla</u> y quitando lo que no queremos que se vea como por ejemplo las etiquetas. </p></li>
            
            
            <br/>
            <center><img src={image5} className="image-permisos-wordpress"  title="imagen permisos wordpress"></img></center>
            <br/><br/>


            <li><p><strong>Ocultar las opciones: </strong>Después de esto volvemos al admin, volvemos al plugin, ajustes y activamos el toggle de <u className="subrayado">ocultar las opciones de plantilla</u>. </p></li>
          </ul>
          <br/><br/>
          <br/><br/>

          <br/>
         
        </div>



      </React.Fragment>

    );
  }

}

export default Wordpress;
